/* stylelint-disable color-function-notation */
.custom-select {
  position: relative;

  select {
    display: none;
  }
}

.select-selected {
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  background-color: inherit;

  &::after {
    content: url("../img/arrow-down.svg");
    position: absolute;
    right: 0;
    top: 9px;
    float: right;
    margin-right: 12px;
    margin-left: 13px;
    width: 24px;
    height: 24px;
  }
}

.select-selected.select-arrow-active::after {
  content: url("../img/arrow-up.svg");
}

.select-items div,
.select-selected {
  padding: 12px 35px 12px 21px;
  color: rgba(0, 0, 0, 100%);
  cursor: pointer;
}

.select-items div {
  // padding-left: 54px;
}

.select-items {
  z-index: 99;
  border: 1px solid var(--alto-color);
  border-radius: 12px;
  box-shadow: 0 9px 14px rgba(0, 0, 0, 20%);
  font-weight: 600;
  font-size: 14px;
  background-color: var(--light-color);
}

// .select-hide {
//   display: none;
// }

.select-items div:hover,
.same-as-selected {
  color: var(--dark-color);
}

.same-as-selected {
  position: relative;

  &::before {
    content: url("../img/check.svg");
    position: absolute;
    left: 0;
    top: 9px;
    float: right;
    margin-right: 12px;
    margin-left: 13px;
    width: 24px;
    height: 24px;
  }
}

.dropdown {
  &-trigger {
    position: absolute;
    right: 10px;
    top: 10px;
  }

  &-wrap {
    position: relative;
  }

  &-list {
    position: absolute;
    left: 0;
    top: calc(100% + 4px);
    z-index: -1;
    overflow-y: auto;
    padding: 8px 12px;
    width: 250px;
    max-height: 250px;
    text-align: left;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s cubic-bezier(0.42, 0, 0.38, 1.34);
    pointer-events: none;

    &.open {
      z-index: 1;
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
    }
  }

  &-option {
    position: relative;
  }

  &-option.active {
    border: none;
    background-color: inherit;

    &::after {
      display: none;
    }
  }

  &-option.selected {
    &::before {
      content: url("../img/check.svg");
      position: absolute;
      left: -20px;
      top: 9px;
      float: right;
      margin-right: 12px;
      margin-left: 13px;
      width: 24px;
      height: 24px;
    }
  }
}
