.tooltip {
  position: relative;
  display: inline-block;
  margin-left: 12px;

  &__text {
    position: absolute;
    top: -12px;
    z-index: 1;
    border-radius: 4px;
    padding: 5px 0;
    width: 118px;
    height: 59px;
    /* stylelint-disable-next-line color-function-notation */
    box-shadow: 0 1px 4px rgba(0, 0, 0, 30%);
    text-align: center;
    color: var(--light-color);
    background-color: rgba($color: #000, $alpha: 70%);
    visibility: hidden;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 14px;
  }

  &__item {
    font-weight: 600;
    font-size: 12px;
    line-height: 24px;
  }

  &:hover &__text {
    visibility: visible;
  }
}
