/* stylelint-disable color-function-notation */
.header {
  position: fixed;
  top: 0;
  z-index: 10000;
  width: 100%;
  background-color: var(--light-color);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 92px;
  }

  &__left {
    display: flex;
    align-items: center;
  }

  &__logo {
    margin-right: 35px;
  }

  &__btn {
    display: flex;
    align-items: center;
    padding: 5px 37px;
    height: 50px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;

    img {
      margin-right: 12px;
    }

    &--catalog {
      display: none;
    }

    &--path {
      padding-left: 16px;
    }

    &--close {
      display: none;
    }

    &--active {
      display: block;
    }

    &--sidebar {
      display: block;
      margin-right: 20px;
      padding: 5px 10px;
      width: 50px;
      background-image: url("../img/hamburger.svg");
      background-position: center;
      background-size: 28px;
      background-repeat: no-repeat;

      &-active {
        background-image: url("../img/close.svg");
      }

      @include for-desktop {
        display: none;
      }
    }
  }

  &__user {
    margin-left: 17px;
    width: 50px;
    height: 50px;
    background-image: url("../img/user.svg");
    background-position: center;
    background-size: 28px;
    background-repeat: no-repeat;
  }

  &__right {
    display: flex;
  }

  &__list {
    display: flex;
  }

  &__item {
    display: flex;
    align-items: center;
    padding-right: 25px;
    padding-left: 15px;

    &:not(:last-child) {
      border-right: 1px solid #d2d2d2;
    }
  }

  &__icon {
    margin-right: 15px;
  }

  &__crit {
    margin-bottom: 2px;
    font-weight: 600;
    font-size: 12px;
    line-height: 14px;
    text-align: left;

    &--btn {
      font-weight: 600;
      font-size: 12px;
      line-height: 14px;
    }
  }

  &__value {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: left;

    &--btn {
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
    }
  }

  &--second {
    box-shadow: 0 0 16px rgba(0, 0, 0, 13%);
  }

  @include tablet {
    &__container {
      display: grid;
      justify-content: center;
      padding-top: 5px;
      padding-bottom: 5px;
      gap: 10px;
    }

    &__item:first-child {
      padding-left: 0;
    }
  }

  @media (max-width: 1099px) {
    &__logo {
      margin-right: 15px;
      max-width: 120px;
    }
  }
}
