.my-data {
  &__item {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #d8d8d8;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;

    span {
      margin-bottom: 6px;
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
      color: #7a7a7a;
    }
  }

  &__bottom {
    padding-top: 38px;
  }
}
