/* stylelint-disable color-function-notation */
.graph-modal {
  display: flex;
  align-items: flex-start;
  padding-top: 92px;
  background-color: rgba($color: #fff, $alpha: 80%);

  &__close {
    width: 28px;
    height: 28px;
    background-image: url("../img/close-black.svg");
    background-position: center;
    background-size: 28px;
    background-repeat: no-repeat;
  }

  &__container {
    position: relative;
    margin: 22px auto;
    border-radius: 18px;
    padding: 23px 27px;
    width: 1070px;
    height: 518px;
    background-color: var(--dark-color);

    &--light {
      border: 1px solid var(--dark-color);
      height: auto;
      background-color: var(--light-color);
    }

    @include tablet {
      border-radius: 0;
      height: auto;
    }
  }

  @include tablet {
    padding-top: 125px;
  }
}

.menu {
  &__list {
    display: grid;
    align-items: start;
    grid-template-columns: 262px 130px 187px 166px 171px;
    gap: 25px;

    @include tablet {
      grid-template-columns: auto;
      gap: 0;
    }
  }

  &__item {
    display: inline-flex;
    flex-direction: column;
    border-style: solid;
    border-width: 1px;
    border-color: #bfbfbf;
    border-radius: 10px;
    padding: 16px 22px;
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &--green {
      border-color: #8ab964;
    }

    &--orange {
      border-color: var(--orange);
    }

    &--yellow {
      border-color: var(--yellow);
    }

    &--light-pink {
      border-color: var(--light-pink);
    }

    &--blue {
      border-color: var(--blue);
    }

    &--purple {
      border-color: var(--purple);
    }

    &--red {
      border-color: var(--red);
    }

    &--pink {
      border-color: var(--pink);

      @include tablet {
        margin-top: 20px;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 9px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: var(--light-color);
  }

  &__link {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    text-align: left;
    color: #bfbfbf;

    span {
      display: flex;
      margin-left: 3px;
    }

    &-icon {
      width: 18px;
      height: 18px;
      fill: inherit;

      &:hover {
        fill: #fafafa;
      }
    }

    &:hover {
      text-decoration: underline;
      color: var(--light-color);
    }

    &--debug {
      margin-bottom: 20px;
      border: 1px solid var(--purple);
      border-radius: 10px;
      padding: 13px 22px;
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      color: var(--light-color);
    }

    &--btn {
      margin-bottom: 20px;
      border: 1px solid var(--yellow);
      border-radius: 10px;
      padding: 13px 22px;
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      color: var(--light-color);

      &:first-child {
        border: 1px solid var(--orange);
      }

      &:last-child {
        border: 1px solid var(--light-pink);
      }
    }

    &--active {
      &-green {
        color: #8ab964;
        fill: #8ab964;
      }

      &-orange {
        color: var(--orange);
        fill: var(--orange);
      }

      &-yellow {
        color: var(--yellow);
        fill: var(--yellow);
      }

      &-light-pink {
        color: var(--light-pink);
        fill: var(--light-pink);
      }

      &-blue {
        color: var(--blue);
        fill: var(--blue);
      }

      &-purple {
        color: var(--purple);
        fill: var(--purple);
      }

      &-red {
        color: var(--red);
        fill: var(--red);
      }

      &-pink {
        color: var(--pink);
        fill: var(--pink);
      }
    }
  }

  &__debug {
    display: flex;
    flex-direction: column;

    &-item {
      display: inline-flex;
      flex-direction: column;
      border-style: solid;
      border-width: 1px;
      border-color: var(--red);
      border-radius: 10px;
      padding: 16px 22px;
    }
  }
}

.popup-payment {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba($color: #fff, $alpha: 80%);

  &__container {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: block;
    margin-top: 120px;
    padding: 45px;
    width: 483px;
    height: 400px;
    box-shadow: 0 18px 64px rgba(0, 0, 0, 20%);
    background-color: #fafafa;

    @include tablet {
      border-radius: 18px;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__img {
    margin-bottom: 20px;
  }

  &__title {
    margin: 0;
    margin-bottom: 28px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 77px;
    max-width: 330px;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
  }

  &__btn {
    padding: 16px 55px 17px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
  }

  &--open {
    display: block;
  }
}
