.filtr {
  border-radius: 18px;

  &__dropdown {
    position: relative;
    border: 1px solid var(--alto-color);
    border-radius: 20px;
    width: 284px;
    height: 42px;

    @media (max-width: 1280px) {
      grid-column: 3 span;
      width: 100%;
    }
  }

  &__title {
    position: relative;
    display: inline-block;
    margin: 0;
    border-top: 1px solid var(--alto-color);
    border-left: 1px solid var(--alto-color);
    border-radius: 18px 18px 0 0;
    padding: 12px 35px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -1px;
      width: 187px;
      height: 1px;
      background-color: var(--light-color);
    }

    &::after {
      content: "";
      position: absolute;
      right: -48px;
      bottom: -1px;
      border-bottom: 1px solid var(--alto-color);
      border-left: 1px solid var(--alto-color);
      border-bottom-left-radius: 18px;
      width: 50px;
      height: 83%;
      background-color: var(--light-color);
    }
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid var(--alto-color);
    border-right: 1px solid var(--alto-color);
    border-bottom: 1px solid var(--alto-color);
    border-left: 1px solid var(--alto-color);
    border-radius: 18px;
    border-top-left-radius: 0;
    padding: 31px 35px;

    @media (max-width: 1280px) {
      display: grid;
      grid-template-rows: repeat(3, 1fr);
      gap: 5px;
    }
  }

  &__btns {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }

  &__btn {
    width: 70px;
    height: 42px;
  }

  &__span {
    display: flex;
    align-items: center;
    justify-content: center;
    border-style: solid;
    border-width: 1px;
    border-radius: 20px;
    width: 100%;
    height: 100%;

    &--orange {
      border-color: var(--orange-color);
    }

    &--green {
      border-color: var(--green-color);
    }

    &--red {
      border-color: var(--red-color);
    }
  }

  &__svg {
    width: 100%;
    height: 100%;

    &--top {
      max-width: 23px;
      max-height: 27px;
      fill: var(--orange-color);
    }

    &--new {
      max-width: 26px;
      max-height: 26px;
      fill: var(--green-color);
    }

    &--sale {
      max-width: 28px;
      max-height: 18px;
      fill: var(--red-color);
    }
  }
}
