.main-content {
  margin-top: 32px;
}

.main-head {
  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 21px;
  }

  &__item {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;

    &:nth-child(1) {
      margin-right: 229px;
      margin-left: 35px;
    }

    &:nth-child(2) {
      margin-right: 112px;
    }

    &:nth-child(3) {
      margin-right: 182px;
    }

    &:nth-child(4) {
      margin-right: 40px;
    }

    @media (max-width: 1280px) {
      font-size: 13px;
      line-height: 120%;

      &:nth-child(1) {
        margin-right: auto;
        margin-left: 50px;
      }

      &:nth-child(2) {
        margin-right: 80px;
      }

      &:nth-child(3) {
        margin-right: auto;
      }

      &:nth-child(4) {
        margin-right: 10px;
      }
    }
  }
}

.main-body {
  position: relative;

  &__accordion {
    display: flex;
    align-items: center;
    padding-left: 16px;
    font-weight: 800;
    font-size: 14px;
    line-height: 17px;
    text-align: left;

    span {
      margin-right: 12px;
    }
  }

  &__panel {
    margin-top: 13px;
    margin-left: 20px;
  }
}
