.custom-checkbox {
  position: relative;
  display: inline-flex;
  flex-direction: row-reverse;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: var(--dark-color);
  cursor: pointer;
  user-select: none;

  &__field {
    position: absolute;
    opacity: 0;
    cursor: pointer;

    &:checked ~ .custom-checkbox__content {
      border: 1px solid var(--dark-color);
      color: var(--dark-color);
    }

    &:checked ~ .custom-checkbox__content::after {
      display: block;
    }
  }

  &__content {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    border: 1px solid #7a7a7a;
    border-radius: 6px;
    width: 20px;
    height: 20px;
    background-color: var(--light-color);

    &::after {
      content: "";
      position: absolute;
      display: none;
      border-radius: 4px;
      width: 12px;
      height: 12px;
      background: var(--dark-color);
    }
  }

  &:hover input ~ &__content {
    background-color: var(--gray-color);
  }
}
