.my-addresses {
  &__body {
    border-radius: 18px;
  }

  &__item {
    display: flex;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;

    &::before {
      content: url("../img/location.svg");
      margin-right: 15px;
      width: 24px;
      height: 24px;
    }

    span {
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
    }

    &:not(:last-child) {
      border-bottom: 1px solid #d8d8d8;
    }
  }
}
