.banners {
  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    gap: 29px;
  }

  &__item {
    height: 326px;
    max-width: 492px;
  }
}

.banner {
  border-radius: 18px;
  width: 100%;
  height: 100%;
  background: #d8d8d8;

  img {
    border-radius: 18px;
    width: 100%;
    height: 100%;
  }
}
