/* stylelint-disable order/order */
.ordering {
  &__container {
    margin-top: 130px;
    max-width: 1015px;
  }

  &__title {
    margin: 0;
    margin-top: 48px;
    margin-bottom: 38px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
  }

  &__total {
    margin-left: 30px;
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
  }

  .product__presence {
    &::after {
      display: none;
    }
  }
}

