    .ordering-item__body {
      border-color: var(--green);
    }

    .ordering-item__title {
      border-color: var(--green);
      color: var(--green);

      &::after {
        content: "";
        border-bottom: 1px solid var(--green);
        border-left: 1px solid var(--green);
      }
    }

    .ordering-item__total {
      border-color: var(--green);
      color: var(--green);

      &::after {
        border-top: 1px solid var(--green);
        border-right: 1px solid var(--green);
