  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-list {
      z-index: 10;
      display: flex;
      justify-content: flex-end;
      margin-right: 55px;

      @include tablet {
        margin-right: 50px;
      }

      @include small-tablet {
        margin-right: 10px;
      }
    }

    &-item {
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;

      &:nth-child(2) {
        margin-left: 148px;
      }

      &:nth-child(3) {
        margin-left: 37px;
      }

      @media (max-width: 1280px) {
        &:nth-child(2) {
          margin-left: 177px;
        }
      }

      @include tablet {
        &:nth-child(2) {
          margin-left: 180px;
        }

        &:nth-child(3) {
          margin-left: 64px;
        }
      }

      @include small-tablet {
        &:nth-child(2) {
          margin-left: 125px;
        }

        &:nth-child(3) {
          margin-left: 26px;
        }
      }
    }
  }

  &__body {
    border-style: solid;
    border-width: 1px;
    border-color: var(--blue-color);
    border-radius: 0 18px;
    padding: 0 28px;

    @include tablet {
      padding: 0 10px;
    }
  }

  &__product {
    &:not(:last-child) {
      border-bottom: 1px solid var(--alto-color);
    }
  }

  &__title {
    position: relative;
    display: inline-block;
    margin: 0;
    border-style: solid;
    border-width: 1px;
    border-color: var(--blue-color);
    border-radius: 18px 18px 0 0;
    padding: 16px 28px 14px;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: var(--blue-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -6px;
      width: 120%;
      height: 15px;
      background-color: var(--light-color);
    }

    &::after {
      content: "";
      position: absolute;
      right: -69px;
      bottom: -2px;
      border-bottom: 1px solid var(--blue-color);
      border-left: 1px solid var(--blue-color);
      border-bottom-left-radius: 18px;
      width: 70px;
      height: 83%;
      background-color: var(--light-color);
    }
  }

  &__bottom {
    display: flex;
    justify-content: flex-end;
  }

  &__total {
    position: relative;
    display: inline-block;
    border-style: solid;
    border-width: 1px;
    border-color: var(--blue-color);
    border-radius: 0 0 18px 18px;
    padding: 14px 34px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: var(--blue-color);

    &::before {
      content: "";
      position: absolute;
      right: 0;
      top: -3px;
      width: 246px;
      height: 19px;
      background-color: var(--light-color);
    }

    &::after {
      content: "";
      position: absolute;
      left: -49px;
      bottom: 11px;
      border-top: 1px solid var(--blue-color);
      border-right: 1px solid var(--blue-color);
      border-top-right-radius: 18px;
      width: 50px;
      height: 83%;
      background-color: var(--light-color);
    }
  }
}

.ordering-footer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  &__item {
    display: flex;
    justify-content: flex-end;
    padding-top: 22px;
    padding-bottom: 13px;
    width: 100%;

    &:not(:last-child) {
      border-bottom: 1px solid var(--alto-color);
    }
  }

  &__btn {
    border-radius: 12px;
    width: 192px;
    height: 50px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;

    &:not(:last-child) {
      margin-right: 31px;
    }
  }
}
