/* stylelint-disable color-function-notation */
.calendar {
  position: relative;

  &__input {
    border: none;
    border-radius: 20px;
    padding: 12px 10px;
    width: 100%;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
  }

  &__icon {
    position: absolute;
    right: 30px;
    top: 10px;
    width: 22px;
    height: 22px;
    pointer-events: none;

    &--close {
      right: 5px;
    }
  }
}

.air-datepicker {
  border: 1px solid var(--alto-color);
  border-radius: 12px;
  box-shadow: 0 9px 14px rgba(0, 0, 0, 20%);
  font-size: 12px;
  background-color: var(--light-color);

  &--content {
    padding: 15px;
  }
}

.air-datepicker-body--day-name {
  display: none;
}

.air-datepicker-body--cells {
  row-gap: 15px;
}

.air-datepicker-body--cells.-days- {
  grid-auto-rows: 25px;
}

.air-datepicker-cell.-selected- {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 100%;
  color: #fff;
  background: var(--light-color);

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    border-radius: 100%;
    width: 25px;
    height: 25px;
    background-color: var(--dark-color);
  }
}

.air-datepicker-cell.-range-from- {
  position: relative;
  z-index: 10;
  margin: 0;
  border-radius: 100%;
  width: 100%;
  background: var(--light-color);
}

.air-datepicker-cell.-in-range- {
  left: -10px;
  margin: 0;
  border-radius: 0;
  width: 160%;
  background-color: var(--alto-color);
}

.air-datepicker-cell.-current- {
  font-weight: 700;
  color: var(--dark-color);
}

.air-datepicker-cell.-selected-.-focus- {
  margin: 0;
  border-radius: 100%;
  background-color: var(--light-color);
}

.air-datepicker-cell.-range-to- {
  border: none;
  border-radius: 5px;
  background-color: var(--gray-color);
}
