.my-orders {
  &__body {
    border-radius: 18px;
    padding-bottom: 0;
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
    margin-top: 40px;

    &-content {
      font-weight: 700;
      font-size: 32px;
      line-height: 38px;
    }
  }

  &__number {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    text-align: center;

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__data {
    display: flex;
    flex-direction: column;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    text-align: center;

    @include small-tablet {
      font-size: 12px;
    }
  }

  &__point {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
  }

  &__sum {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
  }

  &__pay-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    span {
      margin-top: 6px;
      font-weight: 700;
      font-size: 8px;
      line-height: 10px;
    }
  }

  &__order-state {
    display: flex;
    justify-content: center;
  }

  &__btn {
    border-radius: 16px;
    padding: 7px 15px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: var(--light-color);

    &--red {
      background-color: var(--red-color);
    }

    &--pink {
      background-color: #c462f9;
    }

    &--blue {
      background-color: #00afc8;

      @include tablet {
        padding: 7px;
        font-size: 12px;
      }
    }

    &--gray {
      color: #7a7a7a;
      background-color: var(--light-color);
      cursor: default;
    }

    &--green {
      background-color: #58c800;
    }

    &--orange {
      background-color: #fcad08;
    }

    @include small-tablet {
      padding: 7px;
      font-size: 12px;
    }
  }

  &__hide {
    display: flex;
    justify-content: flex-end;
  }
}

.account-filtr {
  display: flex;
  align-items: center;
  margin-bottom: 45px;

  &__title {
    margin: 0;
    margin-right: 35px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;

    @include tablet {
      margin-right: 15px;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 24px;

      @include tablet {
        margin-right: 10px;
      }
    }

    &--type {
      width: 333px;

      @include tablet {
        max-width: 200px;
      }
    }

    &--payment {
      width: 213px;

      @include tablet {
        max-width: 150px;
      }
    }
  }

  &__calendar {
    border: 1px solid var(--alto-color);
    border-radius: 20px;
    width: 230px;
    height: 42px;
  }
}

.table {
  border-spacing: 0;
  width: 100%;

  &__th {
    padding-bottom: 24px;
    font-weight: 600;
    font-size: 12px;
    line-height: 14px;
  }

  &__td {
    border-top: 1px solid #d8d8d8;
    padding: 12px 0;
  }
}
