.login {
  height: 100vh;

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    max-width: 452px;
  }

  &__logo {
    margin: 0;
    margin-bottom: 52px;
  }

  &__form {
    display: flex;
    flex-direction: column;
  }

  &__label {
    margin-bottom: 25px;
  }

  &__input {
    max-width: 452px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    width: 62px;
    height: 62px;
    background-color: var(--dark-color);

    svg {
      width: 28px;
      height: 28px;
      fill: var(--light-color);
    }
  }
}
