.payment {
  &__container {
    margin-top: 150px;
    max-width: 1015px;
  }

  &__title {
    margin: 48px 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
}

.payment-form {
  &__item {
    padding-bottom: 30px;

    &:not(:last-child) {
      border-bottom: 1px solid var(--alto-color);
    }

    &-form {
      display: flex;
      flex-direction: row;
      align-items: flex-start;

      &--column {
        flex-direction: column;
      }
    }

    &-title {
      margin: 0;
      margin-top: 16px;
      margin-bottom: 20px;
      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: flex-end;
  }

  &__btn {
    width: 192px;
    height: 50px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
  }

  &__checkbox {
    &:not(:last-child) {
      margin-right: 40px;
    }

    &--column {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  &__textarea {
    border: 1px solid #bababa;
    border-radius: 12px;
    padding: 17px 21px;
    width: 100%;
    height: 120px;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: var(--dark-color);
    resize: none;

    &::placeholder {
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
      color: rgba($color: #000, $alpha: 50%);
    }
  }
}
