/* stylelint-disable order/order */
/* stylelint-disable color-function-notation */
.product {
  position: relative;

  &__items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;

    &--ordering {
      align-items: flex-start;
      padding-top: 11px;
      padding-bottom: 11px;
    }
  }

  &__left {
    display: flex;
    align-items: center;
    width: 34%;

    @media (max-width: 1280px) {
      width: 28%;
    }
  }

  &__right {
    position: relative;

    &-list {
      width: 67%;

      &::before {
        content: "";
        position: absolute;
        right: 0;
        top: -8px;
        display: none;
        border-right: 1px solid #bababa;
        width: 68%;
        height: 13px;
        background-color: var(--light-color);

        @include tablet {
          width: 72%;
        }
      }

      &::after {
        content: "";
        position: absolute;
        right: 66.9%;
        top: -1px;
        display: none;
        border-top: 1px solid #bababa;
        border-right: 1px solid #bababa;
        border-top-right-radius: 10px;
        width: 30px;
        height: 30px;
        background-color: var(--light-color);

        @include tablet {
          right: 69.8%;
        }
      }

      &--accordion {
        border: 1px solid var(--alto-color);
        border-radius: 12px;
        padding: 10px 30px 10px 10px;

        @media (max-width: 1280px) {
          padding: 10px;
        }
      }

      @include tablet {
        width: 70%;
      }
    }

    &-item {
      display: flex;
      justify-content: space-between;

      &:not(:last-child) {
        padding-bottom: 15px;
      }

      &--main {
        align-items: center;
        padding-bottom: 0;
      }
    }
  }

  &__img {
    position: relative;
    display: inline-block;
    margin-right: 18px;
    border-radius: 8px;
    width: 100%;
    height: 100%;
    max-width: 32px;
    max-height: 32px;
    background-color: #d8d8d8;
    transition: transform 0.2s ease-in-out;
    cursor: help;

    &:hover {
      z-index: 1000000;
      border: none;
      border-radius: 2px;
      box-shadow: 0 0 3px rgba(0, 0, 0, 30%);
      background-color: var(--light-color);
      transform: scale(5) translateY(-10px);
    }

    @media (max-width: 1280px) {
      margin-right: 5px;
    }
  }

  &__title {
    margin: 0;
    max-width: 220px;
    font-weight: 600;
    font-size: 14px;
    line-height: 25px;

    @media (max-width: 1280px) {
      max-width: 200px;
      font-size: 12px;
      line-height: 120%;
    }
  }

  &__icon {
    margin-left: 10px;
    max-width: 30px;
    max-height: 30px;
  }

  &__panel {
    margin-left: 340px;
    max-width: 100%;
    background-color: var(--light-color);
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 117px;
    height: 32px;
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;

    img {
      margin-right: 8px;
    }

    &--close {
      width: 24px;
      height: 24px;
      background-image: url("../img/close-black.svg");
      background-position: center;
      background-size: 22px;
      background-repeat: no-repeat;
    }
  }

  &__residue {
    margin-left: 29px;
    width: 80px;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    text-align: center;

    @media (max-width: 1280px) {
      margin-left: 0;
    }
  }

  &__presence {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 25px;
    width: 150px;
    max-width: 166px;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;

    &::before {
      content: "";
      position: absolute;
      left: 10px;
      border-radius: 100%;
      width: 11px;
      height: 11px;
      background-color: var(--green-color);
    }

    &::after {
      content: "";
      position: absolute;
      right: 25px;
      width: 20px;
      height: 20px;
      background-image: url("../img/arrow-down.svg");
      background-position: center;
      background-size: 20px;
      background-repeat: no-repeat;

      @include tablet {
        right: 38px;
      }
    }

    &-list {
      position: relative;
      display: flex;
      flex-direction: column;
      border-right: 1px solid var(--alto-color);
      border-bottom: 1px solid var(--alto-color);
      border-left: 1px solid var(--alto-color);
      border-radius: 0 0 12px 12px;
      background-color: var(--light-color);
    }

    &-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-left: 40px;
      padding-right: 30px;
      height: 50px;
    }

    &--active {
      &::after {
        background-image: url("../img/arrow-up.svg");
      }
    }

    &--ordering {
      &::before {
        left: 8px;
      }
    }

    &--sub {
      justify-content: flex-start;
      width: 139px;

      &::before {
        content: "";
        position: absolute;
        left: -15px;
        border-radius: 100%;
        width: 11px;
        height: 11px;
        background-color: var(--green-color);
      }
    }

    &--orange {
      &::before {
        background-color: var(--orange-color);
      }

      &::after {
        right: -20px;

        @include tablet {
          right: -5px;
        }
      }
    }

    &--gray {
      &::before {
        background-color: var(--dark-gray-color);
      }
    }

    &--blue {
      &::before {
        background-color: var(--blue-color);
      }

      &::after {
        right: 50px;

        @include tablet {
          right: 60px;
        }
      }
    }

    @include tablet {
      font-size: 12px;
    }
  }

  &__size {
    width: 70px;
    font-weight: 600;
    font-size: 14px;
    line-height: 25px;
    text-align: center;

    &-all {
      width: 70px;
      font-weight: 700;
      font-size: 14px;
      line-height: 25px;
      text-align: center;

      &--ordering {
        margin-left: 10px;
      }

      &--zero {
        color: #999;
      }
    }
  }

  &--main {
    border: 1px solid #bababa;
    border-radius: 12px;
    padding: 2px;
    padding-right: 30px;
    padding-left: 11px;
    height: 52px;

    @media (max-width: 1280px) {
      padding-right: 10px;
    }
  }

  &--none {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      border-radius: 12px;
      width: 100%;
      height: 100%;
      background-color: rgba($color: #fdfdfd, $alpha: 90%);
    }
  }
}

.product-calc {
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-columns: repeat(3, auto);
  width: 117px;
  gap: 5px;

  &__btn {
    border: 1px solid #d6d6d6;
    border-radius: 8px;
    width: 30px;
    height: 30px;
    background-position: center;
    background-size: 22px;
    background-repeat: no-repeat;

    &--minus {
      background-image: url("../img/minus.svg");
    }

    &--plus {
      background-image: url("../img/plus.svg");
    }
  }

  &__value {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;

    span {
      font-weight: 700;
      font-size: 6px;
      line-height: 7px;

      @media (max-width: 1280px) {
        display: none;
      }
    }
  }
}

.product-accordion-content {
  position: relative;
  display: none;
  flex-direction: column;
  align-items: flex-end;

  &--open {
    display: flex;

    .product--main {
      border-bottom-right-radius: 0;
    }

    .product__right-list {
      &::before {
        display: block;
      }

      &::after {
        display: block;
      }

      &--accordion {
        border-radius: 0 0 12px 12px;
      }
    }
  }

  .product__presence {
    &::after {
      display: none;
    }
  }
}
