.account {
  &__container {
    margin-top: 120px;
    max-width: 1015px;

    @include tablet {
      margin-top: 130px;
    }
  }

  &__title {
    margin: 0;
    margin-top: 48px;
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
}

.tabs {
  &__panel {
  }

  &__nav {
    align-items: flex-end;
    margin: 0;
    padding: 0;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -20px;
      z-index: -1;
      border-left: 1px solid #d2d2d2;
      border-radius: 0 20px 0 0;
      width: 0.5px;
      height: 46px;
      background-color: var(--light-color);
    }

    &-btn {
      position: relative;
      left: 0;
      border: 1px solid #d2d2d2;
      border-bottom: 0;
      border-left: 0;
      border-radius: 0 18px 0 0;
      padding: 12px 45px;
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      color: #7a7a7a;
      background-color: var(--light-color);

      &:focus {
        color: var(--dark-color);
        background-color: var(--light-color);
      }

      &--active {
        position: relative;
        left: 0;
        z-index: 10;
        border: 1px solid var(--dark-color);
        border-bottom: 0;
        border-radius: 18px 18px 0 0;
        padding: 17px 55px;
        color: var(--dark-color);
        background-color: var(--light-color);

        &::before {
          content: "";
          position: absolute;
          right: -30px;
          bottom: -1px;
          z-index: 10;
          display: block;
          border-bottom: 1px solid var(--dark-color);
          border-left: 1px solid var(--dark-color);
          border-bottom-left-radius: 12px;
          width: 30px;
          height: 30px;
        }

        &::after {
          content: "";
          position: absolute;
          left: -12px;
          bottom: -1px;
          display: block;
          width: 112%;
          height: 14px;
          background-color: var(--light-color);
        }

        span {
          position: relative;

          &::before {
            content: "";
            position: absolute;
            left: -85px;
            bottom: -18px;
            z-index: 10;
            display: block;
            border-right: 1px solid var(--dark-color);
            border-bottom: 1px solid var(--dark-color);
            border-bottom-right-radius: 12px;
            width: 30px;
            height: 30px;
          }
        }
      }
    }
  }
}

.tabs-section-footer {
  margin-top: 32px;
  padding: 0 28px;

  &__info {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;

    &::before {
      content: url("../img/tooltip-black.svg");
      margin-right: 26px;
      width: 20px;
      height: 20px;
    }
  }

  &__connection {
    padding-left: 47px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
  }
}

.tabs-section-body {
  border: 1px solid var(--dark-color);
  border-radius: 18px;
  border-top-left-radius: 0;
  padding: 40px 28px;
}

.account-tabs {
  &__nav {
    position: relative;

    &-btn {
      &::before {
      }

      &--account {
        left: 0;
        border-left: 1px solid #d2d2d2;
        border-top-left-radius: 18px;
        border-top-right-radius: 0;

        &::after {
          left: 0;
        }

        span {
          display: none;
        }
      }

      &--orders {
        left: -17px;
        z-index: 9;
        border-left: 1px solid #d2d2d2;
        border-top-left-radius: 18px;
      }

      &--address {
        left: -30px;
        z-index: 8;
      }
    }
  }
}

.tabs__nav-btn--active {
  z-index: 10;
  border-left: 1px solid var(--dark-color);
  border-top-right-radius: 18px;
}
