// для использования sass-переменных - удалить root
// colors
:root {
  // base
  --font-family: "RFDewi", sans-serif;
  --content-width: 1330px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --dark-color: #000;
  --gray-color: #f4f4f4;
  --dark-gray-color: #7f7f7f;
  --alto-color: #d2d2d2;
  --green-color: #58c800;
  --orange-color: #fcad08;
  --red-color: #ff1515;
  --blue-color: #5690ff;

  // item-colors
  --green: #89ba5f;
  --orange: #ff4200;
  --yellow: #ffae00;
  --light-pink: #df3c6b;
  --blue: #0089c8;
  --purple: #9b00ff;
  --red: #ff6060;
  --pink: #ff55bc;
}
